/**
 * 角色列表展示
 */
class RoleList extends Component {
  static defaultProps = {
    dataSource: [],
    identity_id: ''
  }

  handleCreate = () => {
    // console.log(this.addModal)
    this.addModal.create()
  }

  handleEdit = () => {
    this.addModal.edit()
  }

  handleChange = (identity_id) => {
    this.props.onChange(identity_id)
  }

  render () {
    const {dataSource} = this.props
    return (
      <div className='role-list'>
        <ul>
          {dataSource.map((item, index) => (
            <li key={index} onClick={() => this.handleChange(item.identity_id)} className={classNames({
              active: this.props.identity_id === item.identity_id
            })}>
              <div>
                <h3>{item.identity_text}</h3>
                <p>{item.identity_id}</p>
              </div>
              <div className="tool">
                  <a href="javascript:;">删除</a>
                </div>
            </li>
          ))}
          <li>
            <a href="javascript:;" onClick={this.handleCreate}>添加角色</a>
          </li>
        </ul>
        <AddModal wrappedComponentRef={(component) => this.addModal = component} />
      </div>
    )
  }
}
/**
 * 视图权限展示
 */
@connect(mapState)
class ViewIdentity extends Component {

  isAuthority (view_id) {
    const {viewFromIdentity} = this.props
    let flg = false;
    viewFromIdentity.forEach(item => {
      if (item.view_id === view_id) {
        flg = true
      }
    })
    return flg
  }

  getViewAuthorityId (view_id) {
    const {view_authority} = this.props
    let view_authority_id = '';
    view_authority.forEach(item => {
      if (item.view_id === view_id) {
        view_authority_id = item.view_authority_id
      }
    })
    return view_authority_id
  }

  handleChange = (e) => {
    // const {viewFromIdentity} = this.props
    // console.log(viewFromIdentity)
    // console.log(e.target)
    console.log(e.target)
    this.props.dispatch({
      type: 'role/setIdentityView',
      view_authority_id: e.target.value,
      identity_id: this.props.identity_id
    })

  }

  renderIdentityList (data) {
    return (
      <ul className={styles.identityList}>
        {data.map((item, index) => (
          <li key={index} className={styles.item}>
          {console.log(this.getViewAuthorityId(item.id), item.id, 'iddddddddd')}
            <Checkbox checked={this.isAuthority(item.id)} value={this.getViewAuthorityId(item.id)} onChange={this.handleChange}>{item.text}</Checkbox>
          </li>
        ))}
      </ul>
    )
  }

  render () {
    const menuTree = getMenuTree()
    
    return (
      <div>
        {menuTree.map((item, index) => {
          return (
            <div className={styles.menuTree} key={index}>
              <h4 className={styles.title}>{item.groupName}</h4>
              {this.renderIdentityList(item.children)}
            </div>
          )
        })}
        
      </div>
    )
  }
}

const Identity = ({identity_id}) => {
  return (
    <Tabs>
      <TabPane tab="视图权限" key="1">
        <ViewIdentity identity_id={identity_id} />
      </TabPane>
      <TabPane tab="接口权限" key="2">Content of tab 2</TabPane>
    </Tabs>
  )
}

/**
 * 角色列表展示
 */
@connect(mapState)
class Role extends Component {
  constructor (props) {
    super(props)
    this.state = {
      identity_id: ''
    }
    console.log(this.props)
  }

  componentDidMount() {
    this.props.dispatch({ type: 'role/fetch' }).then(() => {
      this.setState({
        identity_id: this.props.table.data[0].identity_id
      })
      this.props.dispatch({type: 'role/getViewFromIdentity', identity_id: this.state.identity_id})
    })
    
  }

  handleCreate = () => {
    // console.log(this.addModal)
    this.addModal.create()
  }

  handleEdit = () => {
    this.addModal.edit()
  }

  handleEditAuthorityApiModal = (identity) => {
    this.editAuthorityApiModal.edit(identity)
  }

  addRole () {
    
  }

  // 切换角色
  handleChangeIdentity = (identity_id) => {
    console.log(identity_id)
    this.setState({
      identity_id
    }, () => {
      this.props.dispatch({type: 'role/getViewFromIdentity', identity_id})
    })
  }

  render() {
    const { table } = this.props
    const { identity_id } = this.state
    return (
      <BaseContent>
        <React.Fragment>
          <div className={styles.content}>
            <RoleList dataSource={table.data} identity_id={identity_id} onChange={this.handleChangeIdentity} />
            <div className={styles.tool}>
              <Identity identity_id={identity_id} />
            </div>
          </div>
        </React.Fragment>
        
        <EditAuthorityApiModal wrappedComponentRef={(component) => this.editAuthorityApiModal = component} />
      </BaseContent>
    )
  }
}

export default Role
